.main-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1em;
  padding-right: 1em;
  height: var(--header-height);
  position: relative;
  z-index: var(--z-fixed);
}
.main-logo{
  width: auto;
  height: 65%;
}
.main-nav{
  display: flex;
  align-items: center;
}
.social-network{
  display: block;
  margin-right: 1em;
  width: 2rem;
  height: 2rem;
  svg{
    fill: $color-main;
    transition: fill .3s;
  }
  &:hover{
    &.facebook{
      svg{
        fill: #3b5998;
      }
    }
    &.instagram{
      svg{
        fill: #ff0084;
      }
    }
    & .icon-nav{
      stroke: darken($color-main, 20);
    }
  }
  &.nav{
    height: auto;
    svg{
      transition: stroke .3s;

    }
  }
}
.main-menu{
  $lh: 4em;
  @extend %list-style ;
  position: absolute;
  top: var(--header-height);
  right: 1em;
  background: var(--color-bg);
  overflow: hidden;
  max-height: 0;
  transition: max-height .3s;
  li{

  }
  a{
    display: block;
    text-align: center;
    padding: 0 2.5em;
    line-height: $lh;
  }
  &.show{
    max-height: calc(#{$lh} * 5);
  }
}
