/*
  This boilerplate is based in ITCSS and SMACSS
*/
.main-menu, .slide-navigation {
  list-style: none;
  padding: 0;
  margin: 0;
}

:root {
  --color-main: #5596cc;
  --color-white: #fdfdfd;
  --color-black: #111;
  --header-height: 110px;
  --footer-height: 3rem;
  --color-bg: #fdfdfd;
  --color-font: #222;
  --z-normal: 1;
  --z-fixed: 100;
  --z-modal: 1000;
  --time: 1s;
}

@font-face {
  font-family: 'SF Pro Text';
  src: url("../assets/fonts/SFProText-Regular.woff2") format("woff2"), url("../assets/fonts/SFProText-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bebas Kai';
  src: url("../assets/fonts/BebasKai.woff2") format("woff2"), url("../assets/fonts/BebasKai.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

html, body {
  font-size: 16px;
}

body {
  margin: 0;
  font-family: "SF Pro Text";
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  background: var(--color-bg);
  color: #222;
  min-height: 100vh;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1em;
  padding-right: 1em;
  height: var(--header-height);
  position: relative;
  z-index: var(--z-fixed);
}

.main-logo {
  width: auto;
  height: 65%;
}

.main-nav {
  display: flex;
  align-items: center;
}

.social-network {
  display: block;
  margin-right: 1em;
  width: 2rem;
  height: 2rem;
}

.social-network svg {
  fill: #5596cc;
  transition: fill .3s;
}

.social-network:hover.facebook svg {
  fill: #3b5998;
}

.social-network:hover.instagram svg {
  fill: #ff0084;
}

.social-network:hover .icon-nav {
  stroke: #2b6290;
}

.social-network.nav {
  height: auto;
}

.social-network.nav svg {
  transition: stroke .3s;
}

.main-menu {
  position: absolute;
  top: var(--header-height);
  right: 1em;
  background: var(--color-bg);
  overflow: hidden;
  max-height: 0;
  transition: max-height .3s;
}

.main-menu a {
  display: block;
  text-align: center;
  padding: 0 2.5em;
  line-height: 4em;
}

.main-menu.show {
  max-height: calc(4em * 5);
}

.main-footer {
  margin-top: auto;
  padding: 0 1em;
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.slides-container {
  position: relative;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
}

.slide-item {
  position: absolute;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  transition: opacity 1s;
  opacity: 0;
}

.slide-item.active {
  opacity: 1;
}

.slide-item.active .slide-caption {
  animation: caption .5s 1s linear forwards;
}

@keyframes caption {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.slide-item.active .text {
  transform: translateX(50%) scale(1);
  animation: capText .2s 1.5s linear forwards;
}

@keyframes capText {
  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

.slide-item img {
  width: auto;
  min-width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  object-fit: cover;
  object-position: center;
}

@media screem and (max-width: 1400px) {
  .slide-item img {
    object-position: top;
  }
}

.slide-caption {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  top: 0;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 0 3em;
  opacity: 0;
  z-index: 250;
}

.slide-caption h2, .slide-caption p {
  position: relative;
  z-index: 300;
  margin: 0;
}

.slide-caption .text {
  opacity: 0;
}

.slide-caption h2 {
  margin-bottom: .5rem;
  text-transform: uppercase;
  text-align: center;
}

.slide-caption.right {
  right: 0;
}

.slide-navigation {
  position: absolute;
  bottom: 4em;
  right: 1em;
  display: flex;
  z-index: 500;
}

.slide-navigation li {
  background: #fdfdfd;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin-right: 1em;
  cursor: pointer;
}

.slide-navigation li:last-child {
  margin-right: 0;
}

.slide-navigation li.active {
  background: #5596cc;
}

.social-network svg {
  fill: #5596cc;
  transition: fill .3s;
}

.social-network:hover.facebook svg {
  fill: #3b5998;
}

.social-network:hover.instagram svg {
  fill: #ff0084;
}
