.slides-container{
  position: relative;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
}
.slide-item{
  position: absolute;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  transition: opacity 1s;
  opacity: 0;
  &.active{
    opacity: 1;
    .slide-caption{
      animation: caption .5s 1s linear forwards;
      @keyframes caption{
        to {
          transform: translateX(0);
          opacity: 1;
        }
      }

    }
    .text{
      transform: translateX(50%) scale(1);
      animation: capText .2s 1.5s linear forwards;
      @keyframes capText{
        to {
          opacity: 1;
          transform: translateX(0) scale(1);
        }
      }
    }
  }
  img{
    width: auto;
    min-width: 100vw;
    height: calc(100vh - var(--header-height) - var(--footer-height));
    object-fit: cover;
    object-position: center;
    @media screem and(max-width: 1400px){
      object-position: top;
    }
  }
}
.slide-caption{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  top: 0;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  background: rgba(#000,.8);
  color: #fff;
  padding: 0 3em;
  // transform: translateX(-100%);
  opacity: 0;
  z-index: 250;
  h2,p{
    position: relative;
    z-index: 300;
    margin: 0;
  }
  .text{
    opacity: 0;
  }
  h2{
    margin-bottom: .5rem;
    text-transform: uppercase;
    text-align: center;
  }
  &.right{
    right: 0;
    // transform: translateX(100%);
  }

}
.slide-navigation{
  $size: 1em;
  position: absolute;
  bottom: 4em;
  right: 1em;
  display: flex;
  z-index: 500;
  @extend %list-style;
  li{
    background: $color-bg;
    width: $size;
    height: $size;
    border-radius: 50%;
    margin-right: 1em;
    cursor: pointer;
    &:last-child{
      margin-right: 0;
    }
    &.active{
      background: $color-main;
    }
  }
}
