*,
*::before,
*:after {
  box-sizing: border-box;
}

html, body{
  font-size: 16px;
}

body {
  margin : 0;
  font-family : $font-family;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  background: var(--color-bg);
  color: $color-font;
  min-height: 100vh;
}

a{
  color: inherit;
  text-decoration: none;
  &:hover{
    text-decoration: none;
  }
}
